:root {
  /* Unified color palette with orange tones */
  --color-background: #102424;
  --color-surface: rgba(30, 36, 36, 0.8);
  --color-surface-light: rgba(40, 45, 45, 0.6);
  --color-primary: #CF7A09;
  --color-secondary: #FF6B00;
  --color-accent: #D85604;
  --color-text: rgba(255, 255, 255, 0.87);
  --color-text-secondary: rgba(255, 255, 255, 0.6);
  
  /* Estilos globales de efecto glass */
  --glass-background: rgba(25, 25, 35, 0.3);
  --glass-border: rgba(255, 255, 255, 0.1);
  --glass-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  --glass-blur: blur(12px);
  
  /* Transiciones */
  --transition-smooth: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-bounce: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.App {
  text-align: center;
  background-color: var(--color-background);
  color: var(--color-text);
}

.section {
  height: 100vh;
  min-height: 100vh;
  color: var(--color-text);
  position: relative;
  overflow: auto; /* Allow scrolling within sections if needed */
}
#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}
.image {

  filter: brightness(0) invert(1);
  opacity: .8;
}
.arrow{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
  z-index: 10;
  display: flex;
  justify-content: center;
}
  .bounce {
    -moz-animation: bounce 2s infinite;
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
  }
  
  @keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-30px);
    }
    60% {
      transform: translateY(-15px);
    }
  }


  .glass-card {
  background-color: var(--glass-background);
  backdrop-filter: var(--glass-blur);
  -webkit-backdrop-filter: var(--glass-blur);
  border: 1px solid var(--glass-border);
  box-shadow: var(--glass-shadow);
  border-radius: 16px;
  padding: 1.5rem;
  transition: var(--transition-smooth);
}

.glass-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.3);
}

.gradient-text {
  background: linear-gradient(90deg, var(--color-primary), var(--color-secondary));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
}

.gradient-border {
  position: relative;
  border-radius: 16px;
  overflow: hidden;
}

.gradient-border::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(45deg, var(--color-primary), var(--color-secondary));
  z-index: -1;
  border-radius: 18px;
  animation: border-glow 3s ease-in-out infinite alternate;
}

@keyframes border-glow {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes pulse {
  0% {
    opacity: 0.3;
    transform: scale(0.95);
  }
  100% {
    opacity: 0.5;
    transform: scale(1.05);
  }
}

