/* Hero section styles */
.hero-section {
  min-height: 100vh;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
}

.hero-content,
.hero-content-mobile {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  z-index: 2;
  padding: 0 2rem;
  overflow-x: hidden;
}

.hero-text {
  width: 100%;
  max-width: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
  height: 100%;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  margin: 1rem 0;
  letter-spacing: 0.5px;
  line-height: 1.2;
  color: var(--color-text);
}

.hero-highlight {
  display: inline-block;
  background-image: linear-gradient(
    -225deg,
    var(--color-primary) 0%,
    var(--color-secondary) 29%,
    var(--color-accent) 67%,
    var(--color-primary) 100%
  );
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 5s linear infinite;
  font-size: 3.5rem;
  font-weight: 900;
}

.hero-description {
  font-size: 1.2rem;
  color: var(--color-text-secondary);
  margin-bottom: 2rem;
  max-width: 500px;
}

.subtitle {
  font-size: 1.1rem;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 1rem;
  display: block;
}

.hero-buttons {
  display: flex;
  margin-top: 2rem;
  gap: 1rem;
}

.hero-content-mobile .hero-title,
.hero-content-mobile .hero-highlight {
  font-size: 2.5rem;
}

.hero-content-mobile .hero-description {
  font-size: 1rem;
}

.hero-content-mobile .hero-buttons {
  flex-direction: column;
  gap: 1rem;
}

.hero-content-mobile .hero-buttons button {
  width: 100%;
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}
.text  {
  font-size: 30px;
  z-index: 4;

  
}

.profile:hover { transform: scale(1.1); }

.accelerated.box {
  transition: transform 750ms;
  will-change: transform;
}
.accelerated.box:hover,
.accelerated.box:focus {
  transform: translateY(10px);
}

.janky.box {
  transition: margin-top 750ms;
  will-change: margin-top;
  background: deeppink;
}
.janky.box:hover,
.janky.box:focus {
  margin-top: 10px;
}
